import React from 'react';
import './ErrorPage.css';
import {T} from "./translation";
import {isRouteErrorResponse, useRouteError} from "react-router-dom";
import Navigation from "./Navigation";

function ErrorPage() {

    const error = useRouteError();

    if ((isRouteErrorResponse(error) && error.status === 404) || typeof error === "undefined") {
        return (
            <>
                <Navigation />
                <div className={"box text"}>
                    <T de={
                        <>
                            Die gewhlte Seite wurde nicht gefunden. Bitte drcke die Escape <kbd>ESC</kbd> Taste auf deiner Tastatur um auf die Startseite zurckzukehren.
                        </>
                    } en={
                        <>
                        The selected page was not found. Please press the Escape <kbd>ESC</kbd> key on your keyboard to return to the start page.
                        </>
                    }/>
                </div>
            </>
        );
    }

    return (
        <>
            <Navigation />
            <div className={"box"}>
                <T de={
                    <>
                        Es ist ein Fehler passiert. Bitte drcke die Escape <kbd>ESC</kbd> Taste auf deiner Tastatur um auf die Startseite zurckzukehren.
                    </>
                } en={
                    <>
                        An error happened. Please press the Escape <kbd>ESC</kbd> key on your keyboard to return to the start page.
                    </>
                }/>
            </div>
        </>
    );
}

export default ErrorPage;
