import {Link, Outlet, redirect, useNavigate, useParams} from "react-router-dom";
import React, {createContext, ReactElement, useCallback, useContext} from "react";

export interface TranslationContextPathParams {
    lang: string
}

const tContext = createContext<string>("en");

interface TProps {
    de: string|JSX.Element
    en: string|JSX.Element
}

export function T(data: TProps): JSX.Element {
    const lang = useContext<string>(tContext)
    switch (lang) {
        case "en":
            if (typeof data.en === "string") {
                return <span>{data.en}</span>
            }
            return data.en
        case "de":
            if (typeof data.de === "string") {
                return <span>{data.de}</span>
            }
            return data.de
        default:
            if (typeof data.en === "string") {
                return <span>{data.en}</span>
            }
            return data.en
    }
}

interface LinkLanguageHomeProps {
    to?: string
    children?: React.ReactNode
    id?: string
}

export function TLink(props: LinkLanguageHomeProps) {
    let params = useParams() as Readonly<TranslationContextPathParams>;
    if (typeof params === "undefined") {
        return <Link id={props.id} className={"linkhome"} to={"/"}>{props.children}</Link>
    }
    switch (params.lang) {
        case "en":
        case "de":
            return <Link id={props.id} className={"linkhome"} to={"/" + params.lang + (typeof props.to === "undefined"?null:props.to)}>{props.children}</Link>
        default:
            return <Link id={props.id} className={"linkhome"} to={"/"}>{props.children}</Link>
    }
}

export function TRedirect(to: string) {
    let params = useParams() as Readonly<TranslationContextPathParams>;
    if (typeof params === "undefined") {
        throw new Response("Not Found", { status: 404 });
    }
    switch (params.lang) {
        case "en":
        case "de":
            throw redirect("/" + params.lang + (typeof to === "undefined"?null:to))
        default:
            throw new Response("Not Found", { status: 404 });
    }
}

export function TranslationContext() {
    let params = useParams() as Readonly<TranslationContextPathParams>;
    if (typeof params === "undefined") {
        throw new Response("Not Found", { status: 404 });
    }
    switch (params.lang) {
        case "en":
        case "de":
            break
        default:
            throw new Response("Not Found", { status: 404 });
    }

    return (
        <tContext.Provider value={params.lang}>
            <EscHandler><Outlet /></EscHandler>
        </tContext.Provider>
    )
}

interface EscHandlerProps {
    children: ReactElement<any,any>
}

export function EscHandler(props: EscHandlerProps) {
    let params = useParams() as Readonly<TranslationContextPathParams>;
    let lang = ""
    if (typeof params !== "undefined") {
        switch (params.lang) {
            case "en":
            case "de":
                lang = params.lang
                break
        }
    }

    const navigate = useNavigate()

    const handleKeyDown = useCallback((e: KeyboardEvent) => {
        switch (e.key) {
            case "Escape":
                navigate("/" + lang)
        }
    }, [lang, navigate])

    React.useEffect(() => {
        document.addEventListener('keydown', handleKeyDown)

        return () => {
            document.removeEventListener('keydown', handleKeyDown)
        }
    })

    return props.children
}
