import {Link} from "react-router-dom";
import {ReactComponent as MouseLogo} from "./mouse/icon.svg";
import {ReactComponent as WindowsLogo} from "./windows/icon.svg";
import {T} from "./translation";
import "./LevelSelector.css";
import React, {useCallback, useState} from "react";
import Navigation from "./Navigation";

interface LevelProps {
    active: boolean
    id: number
    icon: JSX.Element
    title: JSX.Element
    subtitle: JSX.Element
    link: string
}

function Level(props: LevelProps) {
    return <Link id={"level-selector__level--" + props.id} to={props.link} className={"level-selector__level" + (props.active?" level-selector__level--selected":"")}>
        <div className={"level-selector__levelimage"}>
            {props.icon}
        </div>
        <h2>{props.title}</h2>
        <p>{props.subtitle}</p>
    </Link>
}

export default function LevelSelector() {
    const [level, setLevel] = useState(0);
    const maxLevels=2;

    const handleKeyDown = useCallback((e: KeyboardEvent) => {
        switch (e.key) {
            case "ArrowRight":
                if (level < maxLevels) {
                    setLevel(level + 1)
                }
                break;
            case "ArrowLeft":
                if (level > 1) {
                    setLevel(level - 1)
                }
                break;
            case "Enter":
                if (level > 0) {
                    const element = document.getElementById("level-selector__level--" + level)
                    if (element != null) {
                        element.click()
                    }
                }
        }
    }, [level])

    React.useEffect(() => {
        document.addEventListener('keydown', handleKeyDown)

        return () => {
            document.removeEventListener('keydown', handleKeyDown)
        }
    })

    return <div className={"level-selector"}>
        <div className={"level-selector__mobile text"}>
            <T en={<>
                <h1>Your device or window is too small!</h1>
                <p>
                    Did you open this website on a small device, or in a small window? Try the following:
                </p>
                <ul>
                    <li>If you are on a <strong>mobile phone</strong>, try switching to a device with a larger screen.</li>
                    <li>If you opened this page on a <strong>tablet</strong>, try rotating it sideways.</li>
                    <li>On a <strong>laptop or PC</strong> with a keyboard, try pressing <kbd>F11</kbd> to open full screen mode. By pressing <kbd>F11</kbd> again, you can return from full screen mode.</li>
                </ul>
            </>} de={
                <>
                    <h1>Dein Gert oder Fenster ist zu klein!</h1>
                    <p>
                        Hast du diese Webseite auf einem zu kleinen Gert oder in einem zu kleinen Fenster geffnet? Probiere:
                    </p>
                    <ul>
                        <li>Wenn du auf einem <strong>Handy</strong> bist, wechsele bitte zu einem Gert mit grerem Bildschirm.</li>
                        <li>Wenn du auf einem <strong>Tablet</strong> bist, versuche es seitwrts zu drehen.</li>
                        <li>Auf einem <strong>greren Computer</strong> mit einer Tastatur, drcke bitte <kbd>F11</kbd> fr den Vollbildschirmmodus. Mit einem weiteren <kbd>F11</kbd> kommst du wieder zurck.</li>
                    </ul>
                </>
            }/>
        </div>
        <Navigation />
        <h1><T en={"Select your level"} de={"Whle deinen Level"}/></h1>
        <T en={
            <div className={"level-selector__intro text box"}>
                <ul>
                    <li>In the <strong>top right corner</strong> you will find the keys you can use on your keyboard.</li>
                    <li>You can always use the Escape <kbd>ESC</kbd> key to return to this screen.</li>
                </ul>
                <p>Use your mouse to click any of the following levels. You can also select them with the left and right arrow keys <kbd>&larr;</kbd> <kbd>&rarr;</kbd> and then press the enter key <kbd>&#x23CE;</kbd>.</p>
            </div>
        } de={
            <div className={"level-selector__intro text box"}>
                <ul>
                    <li>Im <strong>Eck rechts oben</strong> findest du eine Anzeige mit den Tasten deiner Tastatur und ihren Funktionen.</li>
                    <li>Mit der Escape <kbd>ESC</kbd> Taste kannst du immer <strong>zu diesem Bildschirm zurckkehren</strong>.</li>
                </ul>
                <p>Verwende nun deine Maus oder benutze die linken und rechten Pfeiltasten <kbd>&larr;</kbd> <kbd>&rarr;</kbd> auf der Tastatur um ein Level auszuwhlen und drcke dann Enter <kbd>&#x23CE;</kbd>.</p>
            </div>
        }/>
        <div className={"level-selector__list"}>
            <Level
                id={1}
                active={level === 1}
                icon={<MouseLogo/>}
                title={<T de={"Maus"} en={"Mouse"}/>}
                subtitle={<T de={"Lerne deine Maus zu verwenden"} en={"Learn how to use your mouse"}/>}
                link={"mouse"}
            />
            <Level
                id={2}
                active={level === 2}
                icon={<WindowsLogo/>}
                title={<T de={"Fenster"} en={"Windows"}/>}
                subtitle={<T de={"Lerne mit Fenstern zu arbeiten"} en={"Learn how to work with windows"}/>}
                link={"windows"}
            />
        </div>
    </div>
}