import React, {useCallback, useState} from 'react';
import './LangSelector.css';
import {Link} from "react-router-dom";
import {ReactComponent as GermanFlag} from "./german.svg";
import {ReactComponent as EnglishFlag} from "./english.svg";

function LangSelector() {
    const [language, setLanguage] = useState(0);
    const maxLanguages=2;

    const handleKeyDown = useCallback((e: KeyboardEvent) => {
        switch (e.key) {
            case "ArrowRight":
                if (language < maxLanguages) {
                    setLanguage(language + 1)
                }
                break;
            case "ArrowLeft":
                if (language > 1) {
                    setLanguage(language - 1)
                }
                break;
            case "Enter":
                if (language > 0) {
                    const element = document.getElementById("language-selector__language--" + language)
                    if (element != null) {
                        element.click()
                    }
                }
        }
    }, [language])

    React.useEffect(() => {
        document.addEventListener('keydown', handleKeyDown)

        return () => {
            document.removeEventListener('keydown', handleKeyDown)
        }
    })

    return <div className={"language-selector__outer"}>
        <div className={"language-selector__help text box"}>
            <span className={"language-selector__padding"}>
                <kbd>&larr;</kbd> Move selection left / Auswahl nach links
            </span>
            <span className={"language-selector__padding"}>
                <kbd>&rarr;</kbd> Move selection right / Auswahl nach rechts
            </span>
            <span className={"language-selector__padding"}>
                <kbd>&#x23CE;</kbd> Confirm selection / Auswahl besttigen
            </span>
        </div>
        <div className={"language-selector"}>
            <div className={"language-selector__contents"}>
                <Link to={"/de"} className={"language-selector__language" + ((language !== 1)?"":" language-selector__language--selected")} id={"language-selector__language--1"}>
                    <div className={"language-selector__image"}>
                        <GermanFlag/>
                    </div>
                    Weiter auf <span className={"language-selector__languagename"}>Deutsch</span>
                </Link>
                <Link to={"/en"} className={"language-selector__language" + ((language !== 2)?"":" language-selector__language--selected")} id={"language-selector__language--2"}>
                    <div className={"language-selector__image"}>
                        <EnglishFlag/>
                    </div>
                    Continue in <span className={"language-selector__languagename"}>English</span>
                </Link>
            </div>
        </div>
    </div>;
}

export default LangSelector;
