import React, {useCallback, useState} from "react";
import {TLink, T} from "./translation";
import "./Navigation.css";

interface NavOptions{
    lesson:string
    step: number
    maxSteps: number
}

interface NavigationProps {
    nav?: NavOptions
}

export default function Navigation(props: NavigationProps) {
    const handleKeyDown = useCallback((e: KeyboardEvent) => {
        switch (e.key) {
            case "ArrowLeft":
                const back = document.getElementById("navigation__back")
                if (typeof back !== "undefined") {
                    (back as HTMLLinkElement).click()
                }
                break
            case "ArrowRight":
                const forward = document.getElementById("navigation__forward")
                if (typeof forward !== "undefined") {
                    (forward as HTMLLinkElement).click()
                }
                break
        }
    }, [])

    React.useEffect(() => {
        document.addEventListener('keydown', handleKeyDown)

        return () => {
            document.removeEventListener('keydown', handleKeyDown)
        }
    })

    const [fullscreen, setFullscreen] = useState(false)

    return <div className={"navigation"}>
        <div className={"navigation__contents"}>
            <span className={"navigation__item"}>
                <TLink>
                    <kbd>ESC</kbd><T en={"Start page"} de={"Startseite"} />
                </TLink>
            </span>
            <span className={"navigation__divider"}></span>
            <span className={"navigation__item"}>
                <span className={"navigation__padding navigation__button"} onClick={() => {
                    if (fullscreen) {
                        if (document.exitFullscreen) {
                            document.exitFullscreen().then(() => {
                                setFullscreen(false)
                            });
                        }
                    } else {
                        if (document.documentElement.requestFullscreen) {
                            document.documentElement.requestFullscreen().then(() => {
                                setFullscreen(true)
                            })
                        }
                    }
                }}>
                    <kbd>F11</kbd><T en={"Fullscreen"} de={"Vollbildschirm"} />
                </span>
            </span>
            <span className={"navigation__divider"}></span>
            <span className={"navigation__item"}>
                <span className={"navigation__padding"}>
                    <kbd>M</kbd><T en={"Narration"} de={"Sprachbegleitung"} />
                </span>
            </span>
            {typeof props.nav === "undefined"?null:
                <>
                    {props.nav.step === 1?null:<>
                        <span className={"navigation__divider"}></span>
                        <span className={"navigation__item"}>
                            <TLink id={"navigation__back"} to={"/" + props.nav.lesson + "/" + (props.nav.step - 1)}>
                                <kbd>&larr;</kbd>
                                <T de={"Zurck"} en={"Back"} />
                            </TLink>
                        </span>
                    </>}
                    <span className={"navigation__divider"}></span>
                    <span className={"navigation__item"}>
                        <span className={"navigation__padding"}>
                            <T de={<>Schritt {props.nav.step} von {props.nav.maxSteps}</>}
                               en={<>Step {props.nav.step} of {props.nav.maxSteps}</>} />
                        </span>
                    </span>
                    {props.nav.step === props.nav.maxSteps?null:<>
                        <span className={"navigation__divider"}></span>
                        <span className={"navigation__item"}>
                            <TLink to={"/" + props.nav.lesson + "/" + (props.nav.step as number + 1)} id={"navigation__forward"}>
                                <T de={"Forwrts"} en={"Forward"} />
                                <kbd>&rarr;</kbd>
                            </TLink>
                        </span>
                    </>}
                </>
            }
        </div>
    </div>
}