import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import LangSelector from './LangSelector';
import {createHashRouter, redirect, RouteObject, RouterProvider} from "react-router-dom";
import ErrorPage from "./ErrorPage";
import {EscHandler, TranslationContext} from './translation';
import MousePage from "./mouse";
import LevelSelector from "./LevelSelector";

const router = createHashRouter([
    {
        path: "/",
        errorElement: <EscHandler><ErrorPage /></EscHandler>,
        children: [
            {
                index: true,
                element: <LangSelector />
            },
            {
                path: ":lang",
                element: <TranslationContext />,
                errorElement: <EscHandler><ErrorPage /></EscHandler>,
                loader: ({params}) => {
                    switch (params.lang) {
                        case "de":
                        case "en":
                            break
                        default:
                            throw new Response("Not Found", { status: 404 });
                    }
                },
                children: [
                    {
                        index: true,
                        element: <LevelSelector />
                    },
                    lesson("mouse", 5, <MousePage />),
                    {
                        path: "*",
                        element: <ErrorPage />
                    }
                ]
            },
        ]
    },
]);


function lesson(name: string, maxSteps: number, element: JSX.Element): RouteObject {
    return {
        path: name,
        children: [
            {
                index: true,
                loader: ({params}) => {
                    switch (params.lang) {
                        case "de":
                        case "en":
                            throw redirect("/" + params.lang + "/" + name + "/1")
                        default:
                            throw new Response("Not Found", { status: 404 });
                    }
                }
            },
            {
                path: ":step",
                loader: ({params}) => {
                    if (typeof params.step === "undefined") {
                        throw new Response("Not Found", { status: 404 });
                    }
                    const step = parseInt(params.step)
                    if (isNaN(step)) {
                        throw new Response("Not Found", { status: 404 });
                    }
                    if (step > maxSteps) {
                        throw new Response("Not Found", { status: 404 });
                    }
                },
                element: element
            }
        ]
    }
}

function main() {
    const root = ReactDOM.createRoot(
        document.getElementById('root') as HTMLElement
    );

    root.render(
        <React.StrictMode>
            <RouterProvider router={router} />
        </React.StrictMode>
    );
}

if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development' || window.location.host === "levelupcomputers.club") {
    main()
}
